@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,400;1,500;1,600;1,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box;-webkit-tap-highlight-color:transparent}html{font-size:100%}@media only screen and (max-width: 1024px){html{font-size:80%}}@media only screen and (max-width: 600px){html{font-size:60%}}body{font-family:"Montserrat",sans-serif;font-weight:400;line-height:1.5rem;background-color:#0f0f0f;color:#fff}@media only screen and (max-width: 600px){body{margin-bottom:3rem}}a{text-decoration:none;color:unset}@media(hover: hover){a:hover{color:red}}button,input{outline:none}ul{list-style:none}img{max-width:100%}iframe{border:0}.container{max-width:1660px;margin:auto}.mb-3{margin-bottom:3rem}.mb-2{margin-bottom:2rem}.section{padding:0 2rem}.section__header{display:flex;align-items:center;justify-content:space-between}
.logo{font-size:2.5rem;font-weight:600;color:#651fa9;display:flex;align-items:center;justify-content:center}.logo h2{font-size:2rem}.header{height:8rem;position:fixed;top:0;left:0;width:100%;z-index:99;transition:height .3s ease,background-color .3s ease}.header.shrink{height:5rem;background-color:#0f0f0f}.header__wrap{display:flex;align-items:center;justify-content:space-between;height:100%;padding:0 2rem}@media only screen and (max-width: 600px){.header__wrap{justify-content:center}}.header__nav{display:flex;align-items:center;justify-content:unset}.header__nav>*~*{margin-left:2rem}.header__nav li{padding:5px 0;font-weight:700;position:relative;font-size:1.5rem}.header__nav li::after{content:" ";position:absolute;bottom:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:0;transition:width .5s ease;height:2px;background-color:red}.header__nav li.active::after,.header__nav li:hover::after{width:100%}@media only screen and (max-width: 600px){.header__nav{position:fixed;bottom:0;left:0;height:5rem;width:100%;background-color:#0f0f0f;padding:0 2rem;box-shadow:rgba(100,100,111,.2) 0px 7px 29px 0px;display:flex;align-items:center;justify-content:space-between}.header__nav>*~*{margin-left:0}}
.footer{position:relative;padding:6rem 2rem;background-position:top;background-repeat:no-repeat;background-size:cover}.footer__content{max-width:1000px}.footer__content__logo{display:flex;align-items:center;justify-content:center;margin-bottom:3rem}.footer__content__menus{display:grid;grid-template-columns:repeat(3, 1fr)}@media only screen and (max-width: 600px){.footer__content__menus{grid-template-columns:repeat(2, 1fr)}}.footer__content__menu{display:flex;align-items:flex-start;justify-content:flex-start;flex-direction:column;margin-top:1rem;font-size:1.5rem;font-weight:600}.footer__content__menu>*~*{margin-top:1rem}
button{cursor:pointer;font-family:"Montserrat",sans-serif}.btn{border:4px solid transparent;background-color:red;color:#fff;border-radius:30px;padding:.5rem 1.8rem;font-size:1.5rem;font-weight:600;box-shadow:0px 0px 7px 8px #ff00004d;transition:box-shadow .5s ease;position:relative}.btn:hover{box-shadow:0px 0px 7px 15px #ff00004d}.btn-outline{border:3px solid #fff;background-color:transparent;color:#fff;box-shadow:unset;transition:color .3s ease,background-color .3s ease}.btn-outline:hover{box-shadow:unset;color:red;background-color:#fff}.btn.small{border-width:2px;padding:.25rem 1.5rem;font-size:1rem}
.modal{display:flex;align-items:center;justify-content:center;position:fixed;z-index:100;top:0;left:0;right:0;bottom:0;overflow:auto;background-color:rgba(0,0,0,.4);opacity:0;visibility:hidden}.modal.active{opacity:1;visibility:visible}.modal__content{padding:2rem;background-color:#0f0f0f;width:50%;opacity:0;-webkit-transform:translateY(-250px);transform:translateY(-250px);transition:opacity .6s ease,-webkit-transform .6s ease;transition:transform .6s ease,opacity .6s ease;transition:transform .6s ease,opacity .6s ease,-webkit-transform .6s ease;position:relative}@media only screen and (max-width: 1024px){.modal__content{width:80%}}.modal__content__close{position:absolute;top:5px;right:5px;font-size:1.5rem;cursor:pointer}.modal__content__close:hover{color:red}.modal.active .modal__content{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}
.hero-slide{margin-bottom:3rem}.hero-slide__item{padding:9rem 0;width:100%;position:relative;background-position:center;background-repeat:no-repeat;background-size:cover}.hero-slide__item::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.6)}.hero-slide__item::after{content:"";position:absolute;bottom:0;left:0;width:100%;height:100px;background-image:linear-gradient(to top, #0f0f0f, rgba(0, 0, 0, 0))}.hero-slide__item__content{display:flex;align-items:center;justify-content:center;position:relative}.hero-slide__item__content__info{width:55%;padding:0 3rem;position:relative}@media only screen and (max-width: 1024px){.hero-slide__item__content__info{width:100%}}.hero-slide__item__content__info>*~*{margin-top:3rem}.hero-slide__item__content__info .title{font-size:5rem;font-weight:700;line-height:1}@media only screen and (max-width: 1024px){.hero-slide__item__content__info .title{font-size:4rem}}.hero-slide__item__content__info .overview{font-weight:700}.hero-slide__item__content__info .btns>*~*{margin-left:1rem}.hero-slide__item__content__info .title,.hero-slide__item__content__info .overview,.hero-slide__item__content__info .btns{opacity:0;-webkit-transform:translateY(-100%);transform:translateY(-100%);transition:opacity .5s ease,-webkit-transform .5s ease;transition:transform .5s ease,opacity .5s ease;transition:transform .5s ease,opacity .5s ease,-webkit-transform .5s ease}.hero-slide__item__content__poster{flex:1 1;display:flex;align-items:center;justify-content:flex-start;position:relative}.hero-slide__item__content__poster img{width:400px;border-radius:30px;box-shadow:rgba(100,100,111,.2) 0px 7px 29px 0px;-webkit-transform:scale(0);transform:scale(0);transition:-webkit-transform .7s ease;transition:transform .7s ease;transition:transform .7s ease, -webkit-transform .7s ease}@media only screen and (max-width: 1024px){.hero-slide__item__content__poster{display:none}}.hero-slide__item.active .hero-slide__item__content>.hero-slide__item__content__poster img{-webkit-transform:scale(1);transform:scale(1)}.hero-slide__item.active .hero-slide__item__content>.hero-slide__item__content__info .title,.hero-slide__item.active .hero-slide__item__content>.hero-slide__item__content__info .overview,.hero-slide__item.active .hero-slide__item__content>.hero-slide__item__content__info .btns{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}.hero-slide__item.active .hero-slide__item__content>.hero-slide__item__content__info .title{transition-delay:.3s,.3s}.hero-slide__item.active .hero-slide__item__content>.hero-slide__item__content__info .overview{transition-delay:.6s,.6s}.hero-slide__item.active .hero-slide__item__content>.hero-slide__item__content__info .btns{transition-delay:.9s,.9s}
.movie-list .swiper-slide{width:10%}@media only screen and (max-width: 1024px){.movie-list .swiper-slide{width:30%}}@media only screen and (max-width: 600px){.movie-list .swiper-slide{width:40%}}
.movie-card{position:relative;background-position:top;background-repeat:no-repeat;background-size:cover;padding-top:160%;border-radius:30px;margin-bottom:1rem}.movie-card .btn{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%) scale(0);transform:translate(-50%, -50%) scale(0);transition:box-shadow .3s ease,-webkit-transform .3s ease;transition:transform .3s ease,box-shadow .3s ease;transition:transform .3s ease,box-shadow .3s ease,-webkit-transform .3s ease}.movie-card::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background-color:#000;opacity:0;transition:opacity .3s ease;border-radius:30px}@media(hover: hover){.movie-card:hover:before{opacity:1}.movie-card:hover .btn{-webkit-transform:translate(-50%, -50%) scale(1);transform:translate(-50%, -50%) scale(1)}}
.page-header{padding:8rem 0 2rem;text-align:center;margin-bottom:2rem;position:relative;background-position:top;background-repeat:no-repeat;background-size:cover}.page-header h2{position:relative;z-index:99}.page-header::after{content:" ";position:absolute;bottom:0;left:0;width:100%;height:100px;background-image:linear-gradient(to top, #0f0f0f, rgba(0, 0, 0, 0))}
.movie-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-gap:20px;gap:20px;margin-bottom:3rem}@media only screen and (max-width: 1024px){.movie-grid{grid-template-columns:repeat(auto-fill, minmax(150px, 1fr))}}.movie-grid__loadmore{text-align:center}.movie-search{position:relative;width:100%;max-width:500px}.movie-search input{width:100%;padding-right:8rem}.movie-search button{position:absolute;right:0;top:2px}@media only screen and (max-width: 600px){.movie-search button{right:1px;top:1px}}
.input{border:0;background-color:#000;padding:.5rem 1.5rem;font-size:1rem;border-radius:30px;color:#fff;font-family:"Montserrat",sans-serif}

